:root {
  --blue-color: #00acdc;
}

html {
  font-family: Roboto, sans-serif;
}

body {
  background-color: #cfc9c9;
  margin: 0;
}

.header {
  background-color: #fff;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.nav {
  box-sizing: border-box;
  justify-content: flex-start;
  align-items: center;
  max-width: 1000px;
  display: flex;
}

.nav__list {
  flex-wrap: nowrap;
  width: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  flex-shrink: 1;
  margin-right: 20px;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  white-space: nowrap;
  text-overflow: ellipsis;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: block;
  position: relative;
  overflow: hidden;
}

.nav__link:hover, .nav__link.is-active {
  color: var(--blue-color);
}

.nav__link.is-active:after {
  content: "";
  background-color: var(--blue-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.img {
  flex-shrink: 0;
  max-width: 40px;
  max-height: 40px;
  margin-top: 1px;
}
